import _toConsumableArray from 'babel-runtime/helpers/toConsumableArray';
import Carousel from 'veui/components/Carousel.vue';
import Checkbox from 'veui/components/Checkbox.vue';
import RadioGroup from 'veui/components/RadioGroup.vue';
import 'veui-theme-one/components/Checkbox.js';
import 'veui-theme-one/components/Carousel.js';
import 'veui-theme-one/components/radio-group.less';
import 'veui-theme-one/components/checkbox.less';
import 'veui-theme-one/components/carousel.less';


export default {
  name: 'carousel-demo',
  components: {
    'veui-carousel': Carousel,
    'veui-checkbox': Checkbox,
    'veui-radio-group': RadioGroup
  },
  data: function data() {
    return {
      index: 0,
      interval: 3000,
      pauseOnHover: true,
      loose: false,
      autoplay: true,
      wrap: true,
      trigger: 'hover',
      triggers: [{ value: 'hover', label: 'hover' }, { value: 'click', label: 'click' }],
      size: '',
      sizes: [{ value: 'small', label: 'small' }, { value: '', label: 'normal' }, { value: 'large', label: 'large' }],
      indicator: 'radio',
      indicators: [{ value: 'radio', label: 'radio' }, { value: 'number', label: 'number' }, { value: 'tab', label: 'tab' }, { value: 'none', label: 'none' }],
      items: [{
        src: 'http://image.cc0.cn/uploadfile/2017/0612/thumb_1440_0_20170612110044186.jpg',
        alt: 'Highway'
      }, {
        src: 'http://image.cc0.cn/uploadfile/2017/0621/thumb_1440_0_20170621121923346.jpg',
        alt: 'City'
      }, {
        src: 'https://ss0.bdstatic.com/5aV1bjqh_Q23odCf/static/superman/img/logo/bd_logo1_31bdc765.png',
        alt: 'Baidu logo'
      }]
    };
  },

  computed: {
    ui: function ui() {
      return [].concat(_toConsumableArray(this.loose ? ['loose'] : []), _toConsumableArray(this.size ? [this.size] : [])).join(' ');
    }
  }
};


