import Schedule from 'veui/components/Schedule.vue';
import 'veui-theme-one/components/Schedule.js';
import 'veui-theme-one/components/schedule.less';


export default {
  name: 'demo-schedule',
  components: {
    'veui-schedule': Schedule
  },
  data: function data() {
    return {
      selected: {
        0: [[0, 23]],
        1: [[9, 11], [13, 17]],
        3: [[13, 16]],
        5: [[9, 9], [16, 17]] },
      isDisabled: function isDisabled(day, hour) {
        return day === 2 && hour > 11 && hour < 14;
      },
      hourClass: function hourClass(day, hour) {
        return {
          night: hour > 19,
          weekend: day === 6 || day === 0
        };
      },

      shortcuts: [{
        label: '全周',
        selected: {
          0: true,
          1: true,
          2: true,
          3: true,
          4: true,
          5: true,
          6: true
        }
      }, {
        label: '工组日',
        selected: {
          1: true,
          2: true,
          3: true,
          4: true,
          5: true
        }
      }, {
        label: '周末',
        selected: {
          0: true,
          6: true
        }
      }],
      statuses: [{
        label: '已投放',
        name: 'selected'
      }, {
        label: '推荐时段',
        name: 'weekend'
      }, {
        label: '未投放',
        name: 'available'
      }]
    };
  }
};


