

import Icon from './Icon';
import 'veui-theme-one/components/icon.less';
import { input, icons } from '../mixins';
import { patchIndeterminate } from '../utils/dom';

export default {
  name: 'veui-checkbox',
  components: {
    Icon: Icon
  },
  mixins: [input, icons],
  model: {
    prop: 'checked',
    event: 'change'
  },
  props: {
    ui: String,
    trueValue: {
      type: null,
      default: true
    },
    falseValue: {
      type: null,
      default: false
    },
    checked: {
      type: null,
      default: false
    },
    indeterminate: Boolean
  },
  data: function data() {
    return {
      localChecked: this.checked,
      localIndeterminate: this.indeterminate
    };
  },

  computed: {
    attrs: function attrs() {
      return {
        name: this.realName,
        disabled: this.realDisabled || this.realReadonly,
        checked: this.isChecked
      };
    },
    isChecked: function isChecked() {
      return this.localChecked === this.trueValue;
    }
  },
  methods: {
    activate: function activate() {
      this.toggleChecked();
    },
    handleChange: function handleChange() {
      if (this.localIndeterminate) {
        this.localChecked = this.falseValue;
        this.localIndeterminate = false;
      } else {
        this.toggleChecked();
      }
    },
    toggleChecked: function toggleChecked() {
      this.localChecked = this.isChecked ? this.falseValue : this.trueValue;
    }
  },
  watch: {
    indeterminate: function indeterminate(value) {
      this.localIndeterminate = value;
    },
    checked: function checked(value) {
      this.localChecked = value;
    },
    localIndeterminate: function localIndeterminate(value) {
      this.$refs.box.indeterminate = value;
      if (this.indeterminate !== value) {
        this.$emit('update:indeterminate', false);
      }
    },
    localChecked: function localChecked(value) {
      if (this.checked !== value) {
        this.$emit('change', value);
      }
    }
  },
  mounted: function mounted() {
    var box = this.$refs.box;
    box.indeterminate = this.localIndeterminate;
    patchIndeterminate(box);
  }
};


