

import bus from '../bus';
import Button from 'veui/components/Button.vue';
import Icon from 'veui/components/Icon.vue';
import 'veui-theme-one/components/Button.js';
import 'veui-theme-one/components/icon.less';
import 'veui-theme-one/components/button.less';

import 'veui-theme-one/icons/cross-small';
import 'veui-theme-one/icons/check';
import 'veui-theme-one/icons/edit';
import 'veui-theme-one/icons/search';
import 'veui-theme-one/icons/cross';

export default {
  name: 'button-demo',
  components: {
    Icon: Icon,
    'veui-button': Button
  },
  mounted: function mounted() {
    this.$children.forEach(function (child) {
      child.$on('click', function () {
        bus.$emit('log', child.$el.getAttribute('ui'));
      });
    });
  }
};


