import _includes from 'lodash/includes';

import Icon from './Icon';
import 'veui-theme-one/components/icon.less';
import { icons } from '../mixins';

export default {
  name: 'veui-carousel',
  mixins: [icons],
  components: {
    'veui-icon': Icon
  },
  props: {
    datasource: {
      type: Array,
      default: function _default() {
        return [];
      }
    },
    index: {
      type: Number,
      default: 0
    },
    indicator: {
      type: String,
      default: 'radio',
      validator: function validator(value) {
        return _includes(['radio', 'number', 'tab', 'none'], value);
      }
    },
    switchTrigger: {
      type: String,
      default: 'click',
      validator: function validator(value) {
        return _includes(['hover', 'click'], value);
      }
    },
    autoplay: Boolean,
    pauseOnHover: Boolean,
    interval: {
      type: Number,
      default: 3000
    },
    wrap: Boolean
  },
  data: function data() {
    return {
      localIndex: this.index
    };
  },

  computed: {
    count: function count() {
      return this.datasource.length;
    }
  },
  watch: {
    index: function index(value) {
      if (this.localIndex === value) {
        return;
      }
      this.localIndex = value;
      this.initPlay();
    },
    interval: function interval(value) {
      this.initPlay();
    },
    localIndex: function localIndex(value, oldValue) {
      this.$emit('update:index', value);
      this.$emit('change', value, oldValue);
    },
    autoplay: function autoplay(value) {
      this.initPlay();
    }
  },
  methods: {
    step: function step(delta) {
      this.localIndex = (this.localIndex + delta + this.count) % this.count;
    },
    select: function select(index, event) {
      if (event !== this.switchTrigger) {
        return;
      }

      this.localIndex = index;
    },
    initPlay: function initPlay() {
      var _this = this;

      this.stop();
      if (!this.autoplay) {
        return;
      }
      this.__veui_carousel_timer__ = setInterval(function () {
        _this.step(1);
      }, this.interval);
    },
    stop: function stop() {
      clearTimeout(this.__veui_carousel_timer__);
    },
    handleEnter: function handleEnter() {
      if (this.pauseOnHover) {
        this.stop();
      }
    },
    handleLeave: function handleLeave() {
      if (this.pauseOnHover) {
        this.initPlay();
      }
    }
  },
  mounted: function mounted() {
    this.initPlay();
  },
  beforeDestroy: function beforeDestroy() {
    this.stop();
  }
};


